
.md-fab {
    color: white !important;
}
.success-confirm {
    position: absolute;
    background: rgba(25, 168, 0, 0.6) !important;
    border-color: rgba(25, 168, 0, 1) !important;
    width: 150px;
    text-align: center;
    color: white;
    top: 70px;
    right: 5%;
}
.error-confirm {
    position: absolute;
    background: rgba(255, 87, 51, 0.6) !important;
    border-color: rgb(255, 87, 51) !important;
    width: 100px;
    text-align: center;
    color: white;
    top: 70px;
    right: 5%;
}
.md-chip {
    height: unset !important;
}
.info-text {
    font-size: 20px;
    position: relative;
    top: 2px
}




