.button[data-v-1c993664] {
        position: relative;
        display: inline-block;
        cursor: pointer;

        -webkit-transition: all 0.4s ease-in;
        transition: all 0.4s ease-in;
}
.button[data-v-1c993664]:before {
        content: '';
        background-color: #3F51B9;
        border-radius: 50%;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        -webkit-transform: scale(0.001, 0.001);
        transform: scale(0.001, 0.001);
}
.button[data-v-1c993664]:focus {
        outline: 0;
}
.button[data-v-1c993664]:focus:before {
        -webkit-animation: effect_dylan-data-v-1c993664 0.8s ease-out;
        animation: effect_dylan-data-v-1c993664 0.8s ease-out;
}
@-webkit-keyframes effect_dylan {
50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
}
99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
}
100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
}
}
@keyframes effect_dylan-data-v-1c993664 {
50% {
            -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
            opacity: 0;
}
99% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 0;
}
100% {
            -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
            opacity: 1;
}
}





