.drop-down-enter-active {
    animation: drop-down .5s !important;
}

.drop-down-leave-active {
    animation: drop-down .5s reverse  !important;
}

@keyframes drop-down {
    from {
        transform: skewY(0) translate(0, -25%) !important;
        opacity: 0 !important;
    }
    to {
        transform: skewY(0) translate(0, 0) !important;
        opacity: 1 !important;
    }
}

