.floaty {

    position: fixed;
    bottom: 15px;
    right: 15px;
    z-index: 99999999;
}

.floaty-list {
    margin: 0 0 20px;
    padding: 0 0 0 6px;
    list-style: none;
    opacity: 0;
    display: none;
    transition: opacity .2s ease-out;
}

.floaty:hover .floaty-list {
    opacity: 1;
    display: block;
}

.floaty-list-item {
    position: relative;
    width: 40px;
    height: 40px;
    margin: 15px 5px;
    cursor: pointer;
    background-color: #f0f0f0;
    border-radius: 50%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, .25);
}

.floaty-list-item > img {
    border-radius: 50%;
}

.floaty-list-item--yellow {
    background-color: #50d830;
    /*background: linear-gradient(to bottom, #50d830 0%, #679868 100%);*/
}

.floaty-list-item--yellow > svg {
    fill: #fff;
}

.floaty-list-item--blue {
    background-color: #5ec866;
}

.floaty-list-item--blue > svg {
    fill: #fff;
}

.floaty-list-item-label {
    position: absolute;
    top: 8px;
    right: 120%;
    padding: 4px 8px;
    font-size: 14px;
    color: #fff;
    background-color: #424242;
    border-radius: 2px;
    opacity: 0;
    white-space: nowrap;
    pointer-events: none;
    transition: opacity .2s ease-out;
}
.btn-menu:active:not([disabled]) {
    background-color:unset !important;
}
.btn-menu:hover:not([disabled]):not(.md-raised) {
    background-color:unset !important;
    text-decoration: none;
}

.floaty-list-item:hover > .floaty-list-item-label {
    opacity: 1;
}

.floaty-btn {
    position: relative;
    width: 56px;
    height: 56px;
    float: right;
    /*background: linear-gradient(to bottom, #0060ae 0%, #415361 100%);*/
    cursor: pointer;
    background-color: #0060ae;
    border-radius: 50%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, .25);
}

.floaty-btn:hover .floaty-btn-label {
    opacity: 1;
}

.floaty-btn-label {
    position: absolute;
    top: 14px;
    right: 115%;
    padding: 4px 8px;
    font-size: 14px;
    color: #fff;
    background-color: #424242;
    border-radius: 2px;
    opacity: 0;
    pointer-events: none;
    white-space: nowrap;
    transition: opacity .2s ease-out;
}

.floaty-btn-icon {
    transition: all .2s;
}

.floaty-btn-icon-plus {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}

.floaty.is-active .floaty-btn-icon-plus {
    opacity: 0;
    -webkit-transform: rotate(13deg);
    transform: rotate(130deg);
}

.floaty-btn-icon-create {
    opacity: 0;
    -webkit-transform: rotate(-60deg);
    transform: rotate(-60deg);
}

.floaty.is-active .floaty-btn-icon-create {
    opacity: 1;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.one, .two {
    position: absolute;
    border-radius: 3px;
    background: #fff;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 400ms all ease-out;
    transition: 400ms all ease-out;
}

.one {
    top: 30px;
    left: 20px;
    right: 20px;
    height: 5px;
}

.two {
    top: 20px;
    left: 30px;
    bottom: 20px;
    width: 5px;
}

.icon {
    position: relative;
    width: 65px;
    height: 65px;
    background: #3F51B9;
    border-radius: 100px;
}

.icon:hover {
    cursor: pointer;
}

.floaty:hover .two {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}

.fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
}

