.tooltip {
    position: relative;
    display: inline-block;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 80px;
    background-color: rgba(0, 0, 0, 0.777);
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 80%;
    left: 135%;
    margin-left: -59px;
}

.tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.677) transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}
.tooltip-table {
    position: relative;
    /*display: inline-block;*/
}

.tooltip-table .tooltiptext {
    visibility: hidden;
    width: 80px;
    background-color: rgba(0, 0, 0, 0.777);
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    bottom: 80%;
    left: 95%;
    margin-left: -150%;
}

.tooltip-table .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.677) transparent transparent transparent;
}

.tooltip-table:hover .tooltiptext {
    visibility: visible;
}

